.App{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.userInput{
  width: 300px;
  height: 30px;
  border-radius: 20px;
  font-size: 20px;
  font-weight: 600;
  padding-left: 10px;
}

.submit_app{
  width: 130px;
  height: 40px;
  margin-left: 20px;
  background-color: cornflowerblue;
  border-radius: 20px;
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
}

.recipe-conatiner{
  margin-top: 25px;
  display: grid;
  grid-template-columns: 280px 280px 280px;
  align-items: baseline;
}